<template>
  <div class="component py-4">
    <h5 class="component__heading pt-0 pt-lg-2 px-3 px-lg-5">Group Contribution</h5>
    <div class="row">
      <div class="col-lg-9 col-xl-7">
        <BetForm class="px-3 px-lg-5">
          <form @submit.prevent="sendContribution">
            <div class="form-group" v-if="contribution.formErrors">
              <span class="text-danger">The fields cannot be empty</span>
            </div>
            <div class="form-group">
              <input v-model="contribution.amount" type="text" class="form-control" placeholder="Amount to contribute to group">
            </div>
            <div class="form-group">
              <select v-model="contribution.group" class="form-control">
                <option selected value=""> Select group </option>
                <option v-for="group in userGroups" :value="group.uuid" :key="group.name">{{group.name}}</option>
              </select>
            </div>
            <div class="form-group">
              <BetButton type="submit" :disabled="!contribution.group">Contribute</BetButton>
            </div>
          </form>
        </BetForm>
      </div>
    </div>
  </div>
</template>

<script>
  import { GROUP_CONTRIBUTE } from '../../api_routes'
  import axios from 'axios'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm'
  import BetButton from '../Utilities/BetButton'

  export default {
    name: 'Contribution',
    components: {BetForm, BetButton},
    props: ['authUser', 'userGroups'],
    data () {
      return {
        contribution: {
          amount: '',
          group: '',
          formErrors: false
        }
      }
    },
    methods: {
      sendContribution () {
        if (this.contribution.group !== null && this.contribution.amount !== null) {
          axios.post(GROUP_CONTRIBUTE + this.contribution.group + '/contribute', this.contribution, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
            console.log(response.data)
            if (response.data.code === 200) {
              this.$noty.success(response.data.message)
              this.$store.dispatch('init_auth_user', {request: 'refresh'})
            } else {
              this.$noty.error(response.data.message)
            }
          }, (error) => {
            this.$noty.error(error.response.data.message)
          })
        } else {
          this.contribution.formErrors = true
        }
      }
    }
  }
</script>

