<script>
  import { INVITE_URL } from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import {mapGetters} from 'vuex'
  import axios from 'axios'
  import Contribution from './Contribution'
  import VotingComponent from './VotingComponent'
  import GroupBetHistory from './GroupBetHistory'
  import GroupInvitations from './GroupInvitations'
  const GroupList = () => import('./GroupList')
  const CreateGroup = () => import('./CreateGroup')

  export default {
    name: 'group',
    components: {GroupInvitations, GroupBetHistory, VotingComponent, Contribution, GroupList, CreateGroup},
    computed: {
      ...mapGetters(['authUser', 'userDetails', 'userGroups'])
    },
    data () {
      return {
        invitee: '',
        friends: '',
        suggestion: {
          game: '',
          group: ''
        },
        suggestErrors: [],
        markets: [{'name': 'Match Results'}, {'name': 'Double Chance'}, {'name': 'Over Under'}]
      }
    },
    methods: {
      addToVoted (vote) {
        this.votes.push(vote.game_id)
      },
      placeGroupBet () {
        console.log('Place Group bet')
      },
      inviteFriend () {
        let postData = {
          invitee: this.invitee
        }
        axios.post(INVITE_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
          if (response.data.status === 200) {
            this.$noty.success('Friend notified, and invited successfully')
            this.invitee = ''
          } else {
            this.$noty.error(response.data.message)
          }
        })
      }
    }
  }
</script>

