<template>
  <div class="row">
    <div class="col-md-12">

      <BetNavTabs class="px-3 px-lg-5">
        <ul class="nav nav-sm" id="pills-tab" role="tablist">
          <li class="nav-item">
            <a class="nav-link active" data-toggle="pill" href="#my-invites" role="tab" aria-controls="pills-home" aria-selected="true">My Invites</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="pill" href="#my-invited-friends" role="tab" aria-controls="pills-home" aria-selected="true">Requested Friends</a>
          </li>
          <li class="nav-item">
            <a class="nav-link"  data-toggle="pill" href="#invite-user" role="tab" aria-controls="pills-profile" aria-selected="false">Invite a Friend</a>
          </li>
        </ul>
      </BetNavTabs>

      <div class="tab-content" id="pills-tabContent">
        <div class="tab-pane fade show active" id="my-invites" role="tabpanel" aria-labelledby="pills-home-tab" v-if="userDetails">
          <ul class="px-3 px-lg-5 mb-0">
            <li class="li-invites" v-if="userDetails !== null && userDetails.invited" v-for="group in userDetails.invited.data" :key="group.name">
              {{ group.group_name }}
              <button class="btn btn-sm btn-warning"
                    v-if="group.accepted === 0"
                    @click="acceptInvitation(group.group_uuid)">
                Accept
              </button>
              <button class="btn btn-sm btn-warning"
                    v-else
                    @click="leaveGroup(group.group_uuid)">
                Leave
              </button>
            </li>
          </ul>

        </div>

        <div class="tab-pane fade show" id="my-invited-friends" role="tabpanel" aria-labelledby="pills-home-tab">
          <ul class="px-3 px-lg-5 mb-0">
            <li class="li-invites" v-for="invite in authUser.group.invites.data" :key="invite.id">
              {{ invite.invited }}
              <button class="btn btn-warning">
                pending
              </button>
            </li>
          </ul>
        </div>

        <div class="tab-pane fade py-4" id="invite-user" role="tabpanel" aria-labelledby="pills-profile-tab">
          <div class="row">
            <div class="col-lg-9 col-xl-7">
              <BetForm class="px-3 px-lg-5">
                <form @submit.prevent="inviteFriend">
                  <div class="form-group">
                    <div class="input-group mb-3">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1"><i class="fa fa-plus"></i></span>
                      </div>
                      <input v-model="invitee" type="text" class="form-control" placeholder="Add a friend number">
                    </div>
                  </div>
                  <div class="form-group">
                    <BetButton type="submit">Send Invitation</BetButton>
                  </div>
                </form>
              </BetForm>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import {INVITE_URL, GROUP_ACCEPT_URL} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import axios from 'axios'
  import BetForm from '../Utilities/BetForm'
  import BetButton from '../Utilities/BetButton'
  import BetNavTabs from '../Utilities/BetNavTabs'

  export default {
    name: 'GroupInvitations',
    props: ['authUser', 'userDetails'],
    components: {BetButton, BetForm, BetNavTabs},
    data () {
      return {
        invitee: '',
        friends: ''
      }
    },
    methods: {
      inviteFriend () {
        let invites = []

        let users = {
          invitee: this.invitee
        }
        axios.post(INVITE_URL, users, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
          if (response.data.status === 200) {
            this.$noty.success(response.data.message)
            this.invitee = ''
            invites.push(this.invitee)
          } else {
            this.$noty.error(response.data.message)
          }
        })
      },
      acceptInvitation (group) {
        let postData = {
          group_id: group,
          accepted: true
        }
        axios.post(GROUP_ACCEPT_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
          console.log(response)
          if (response.status === 200) {
            this.$noty.success('Friend notified, and invited successfully')
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
          }
        })
      }
    },
    leaveGroup (group) {
      console.log('Leave group method', group)
    }
  }
</script>

<style scoped lang="stylus">
  .li-invites
    list-style none
    padding 8px 0
    margin 0
    display flex
    align-items center
    border-bottom 1px solid rgba(#fff, .2)

    &:last-child
      border 0

    button
      margin-left auto

  @media (max-width 767px)
    .nav-sm
      display flex
      align-items flex-end

    .nav-item
      width 33%
</style>
