<template>
  <BetAccordion v-if="userGroups && userGroups.length">
    <b-card no-body class="mb-1 user-group" v-for="(group, index) in userGroups" :key="group.uuid">
      <b-card-header header-tag="header" role="tab" v-b-toggle="`group${group.uuid}`">
        <div class="right-arrow float-right">+</div>
        <span class="header-anchor">{{ group.name }} {{ group.admin ? '(admin)' : ''}}</span>
      </b-card-header>
      <b-collapse :id="`group${group.uuid}`" :visible="index === 0" accordion="my-accordion" role="tabpanel">
        <b-card-body>
          <div class="table-responsive user-group__table">
            <Table v-if="group.group_picks">
              <thead>
                <tr>
                  <th class="pl-3 pl-lg-4">Time</th>
                  <th>Game</th>
                  <th>Pick</th>
                  <th>User</th>
                  <th>Status</th>
                  <th>Votes</th>
                  <th>vote</th>
                </tr>
              </thead>
              <tbody>
                <tr 
                v-for="(pick, index) in group.group_picks.data" 
                v-if="!$gameHelper.groupGameIsStarted(pick)" 
                :key="index">
                  <td class="pl-3 pl-lg-4">
                    <span class="date__time"> {{ pick.scheduled | formatTime }} </span> <br>
                    <span class="date__day"> {{ pick.scheduled | betDate }} </span>
                  </td>
                  <td>{{pick.game}}</td>
                  <td>
                    {{pick.pick}}
                    <span class="date__day"> (Odd : {{ pick.odd }})</span>
                  </td>
                  <td>+{{pick.user}}</td>
                  <td>{{ $gameHelper.groupGameIsStarted(pick) ? 'Started' : 'Not Started' }}</td>
                  <td> {{ pick.votes }} </td>
                  <td>
                    <input type="checkbox"
                          :value="pick.id"
                          v-if="!$gameHelper.groupGameIsStarted(pick)"
                          :disabled="userHasVotedForGame(pick)"
                          @click="addToVoted(pick)"/>
                  </td>
                </tr>
              </tbody>
            </Table>
          </div>
          <div class="table-responsive user-group__table">
            <Table>
              <thead>
                <tr>
                  <th v-if="group.admin" class="pl-3 pl-lg-4">Stake </th>
                  <th v-if='group.admin'>P-Win <br> <small class="date__day">For un-started games</small></th>
                  <th v-if='group.admin'>P-Win <br><small class="date__day">For all games</small></th>
                  <th>
                    Odds<br>
                    <small class="date__day">Un-started games </small>
                  </th>
                  <th>
                    Odds<br>
                    <small class="date__day">For all games </small>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td class="pl-3 pl-lg-4" v-if='group.admin'>
                    <input class="form-control form-control-sm" type="number" v-model="stake" id="user_stake"/>
                  </td>
                  <td v-if='group.admin'>{{ getPossibleWin(group) }}</td>
                  <td v-if='group.admin'>{{ getPossibleWinForAllGames(group) }}</td>
                  <td>
                    {{ getTotalOdds(group) }}
                  </td>
                  <td>
                    <!-- {{ getTotalOddsForAllGames (group) }} -->
                  </td>
                </tr>
              </tbody>
            </Table>
          </div>
          <div class="form-group d-flex justify-content-between">
            <BetButton type="submit" @click="submitVotes(group.uuid)">Submit Votes</BetButton>
            <BetButton v-if="group.admin" type="submit" @click="placeGroupBet(group)">Place Group Bet</BetButton>
          </div>
        </b-card-body>
      </b-collapse>
    </b-card>
  </BetAccordion>
</template>

<script>
  import axios from 'axios'
  import {GROUP_PLACE_BET, GROUP_VOTED_GAMES_URL} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import {GAME} from '../../games'
  import Table from '../Utilities/Table'
  import BetAccordion from '../Utilities/BetAccordion'
  import BetButton from '../Utilities/BetButton'

  export default {
    name: 'VotingComponent',
    components: { Table, BetAccordion, BetButton },
    props: ['authUser', 'userGroups'],
    data () {
      return {
        votes: [],
        stake: 100,
        voters: []
      }
    },
    methods: {
      submitVotes (groupId) {
        console.log('Group id', groupId)
        let postData = {
          votes: this.votes,
          group_id: groupId
        }
        axios.post(GROUP_VOTED_GAMES_URL, postData, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
          console.log(resp.data)
          this.$noty.success('Votes submitted successfully')
          this.$store.dispatch('init_auth_user', {request: 'refresh'})
        })
      },
      getGameVoters (game) {
        if (!game) return
        let voters = game.voters.data
        let gameVoters = []
        voters.forEach((voter) => {
          gameVoters.push(voter.user_id)
        })
        return gameVoters
      },
      userHasVotedForGame (game) {
        if (!game) return
        let userVotedForGame = true
        let gameVoters = this.getGameVoters(game)
        if (gameVoters.indexOf(this.authUser.user_id) === -1) {
          userVotedForGame = false
        } else {
          userVotedForGame = true
        }
        return userVotedForGame
      },
      addToVoted (vote) {
        this.votes.push(vote.game_id)
      },
      getTotalOddsForAllGames (group) {
        if (!group) return
        let odds = 1
        console.log('Groups Found for picks', group)
        if (group) {
          group.group_picks.data.forEach((pick) => {
            console.log('sample pick', pick.odd)
            odds *= parseFloat(pick.odd)
            console.log('Odd value')
          })
        }
        return odds.toFixed(2)
      },
      getTotalOdds (group) {
        if (!group) return
        let odds = 1
        let groupPicks = group.group_picks.data
        console.log('Groups Found', groupPicks)
        if (groupPicks) {
          groupPicks.forEach((pick) => {
            if (!GAME.groupGameIsStarted(pick)) {
              console.log('sample pick', pick.odd)
              odds *= parseFloat(pick.odd)
              console.log('Odd value')
            }
          })
        }
        return odds.toFixed(2)
      },
      getPossibleWin (group) {
        if (!group) return
        return this.stake ? this.getTotalOdds(group) * this.stake : 0
      },
      getPossibleWinForAllGames (group) {
        if (!group) return
        return this.stake ? this.getTotalOddsForAllGames(group) * this.stake : 0
      },
      placeGroupBet (group) {
        if (!group) return
        let reviewBet = false
        let bets = this.authUser.group.group_picks.data
        console.log('selections for the group bet', bets)
        let picks = []
        bets.forEach((bet) => {
          if (GAME.groupGameIsStarted(bet.game)) { reviewBet = true }
          if (!GAME.groupGameIsStarted(bet.game) && bet.votes >= 2) {
            picks.push({
              game_id: bet.game_id,
              game: bet.game,
              odd: bet.odd,
              id: bet.oddId,
              odd_id: bet.odd_id,
              pick: bet.pick,
              scheduled: bet.scheduled,
              market_id: bet.market_id
            })
          }
        })
        if (reviewBet) {
          this.$noty.warning('Some of the games in your bet slip have already started')
          return
        }
        console.log('selections for the group bet after filtering', bets)
        let postData = {
          group_id: group.uuid,
          picks: picks,
          amount: this.stake
        }
        axios.post(GROUP_PLACE_BET, postData, {headers: AUTH_CONFIG.getHeaders()}).then(resp => {
          console.log(resp.data)
          if (resp.data.code === 200) {
            this.$noty.success(resp.data.message)
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .user-group
    &__table
      margin 0 -20px
      width calc(100% + 40px)

    .form-control
      background-color #111d39
      border-color #0d386f
      color #fff
      min-width 64px

      &::-webkit-input-placeholder
        color #fff
      &::-moz-placeholder
        opacity 1
        color #fff
      &:-ms-input-placeholder
        color #fff

    th
      .date__day
        display inline-block
        line-height 1.2
        white-space normal !important
</style>
